	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter - Forty by HTML5 UP","author":"Hunter Chang"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/one-week-get-to-grips-with-adobe-air/index.md absPath of file >>> MarkdownRemark","html":"<p><em>\"James!\"</em> they say, <em>\"you've been dying to play with Flex and Air.  Make us an application to accompany our E-Newsletter!\"</em>  How could I refuse?</p>\n<p><em>\"You have until friday.\" </em></p>\n<p>So that's what I've been doing.</p>\n<p style=\"text-align: center\"><img src=\"http://uploads.psyked.co.uk/2007/08/flexair.jpg\" alt=\"flexair.jpg\"></p>\n<p style=\"text-align: center\">&#xA0;</p>\nI have unfortunately, no previous experience with Flex, Air or even ActionScript 3, so this is proving to be interesting.  Marketing has settled on producing an RSS reader - stay tuned and I&apos;ll let you know how things turn out in couple o&apos; days.","frontmatter":{"title":"One week get to grips with Adobe AIR...","date":"August 01, 2007","tags":["flex","actionscript","air"]}}},"pathContext":{}}

